import React, { Component } from 'react'; 
import { withRouter } from 'react-router-dom';


//bootstrap
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import CarbonxPages from '../../main/carbonx_pages';
import ccx_explain from '../../../images/homepage600.png';

//lang & styles
import './home.css';

const thisPage = CarbonxPages.filter(el => el.key==="hom")[0];

class HomePage extends Component {
    // notes:

    render(){
    
        return (
            <>
                <Row><Col>{thisPage.icon}&nbsp; {thisPage.title}</Col></Row>
                <Row className='home-headline-row'><Col sm={12} md={9} className='home-headline'>Take Control and Be a Part of the Solution</Col></Row>
                <Row className='home-headline-row'><Col sm={12} md={9} className='home-content-list'><img src={ccx_explain} alt="Buy Carbon Credits - Redeem for Merch" /></Col></Row>
                <Row className='home-headline-row-list'><Col sm={12} md={9} className='home-content-list'>&#x2022; Buy cleaned carbon credits from Green companies</Col></Row>
                <Row className='home-headline-row-list'><Col sm={12} md={9} className='home-content-list'>&#x2022; Redeem for discounts, merchandise, vouchers to offset emissions</Col></Row>
                <Row className='home-headline-row-list'><Col sm={12} md={9} className='home-content-list'>&#x2022; Or, gift them to friends, family, and loved ones!</Col></Row>
            </>
        );
    }
}


export default withRouter(HomePage);
//export default HomePage;

